<template>
  <div class="roleManger">
    <div class="web-roleManger web-business">
      <el-table :data="tableData" style="width: 100%">
        <el-table-column label="角色名" prop="role"></el-table-column>
        <el-table-column label="描述" prop="description"></el-table-column>
        <el-table-column align="right">
          <template slot="header">
            <el-button class="handleAdd" size="larger" @click="handleAddAction">新增角色</el-button>
          </template>
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEditAction(scope.$index, scope.row)">编辑</el-button>
            <el-button size="mini" @click="handleAllocationAction(scope.$index, scope.row)">授权</el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDisableAction(scope.$index, scope.row)"
            >删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog
      :title="handleTitle"
      :visible.sync="handleAddVisible"
      width="420px"
      :before-close="handleAddClose"
    >
      <el-form :model="addForm" :rules="addFormRules" ref="addForm" label-width="72px">
        <el-form-item label="角色名" prop="role">
          <el-input v-model="addForm.role" autocomplete="on"></el-input>
        </el-form-item>
        <el-form-item label="描述" prop="description">
          <el-input v-model="addForm.description" autocomplete="on"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleCancleAction('addForm')">取 消</el-button>
        <el-button type="primary" @click="handleAddDone('addForm')">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog
      :title="editTitle"
      :visible.sync="editVisible"
      width="420px"
      :before-close="editClose"
    >
      <el-form :model="editForm" :rules="editFormRules" ref="editForm" label-width="72px">
        <el-form-item label="角色名" prop="editRole">
          <el-input v-model="editForm.editRole" autocomplete="on"></el-input>
        </el-form-item>
        <el-form-item label="描述" prop="editDescription">
          <el-input v-model="editForm.editDescription" autocomplete="on"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editAction('editForm')">取 消</el-button>
        <el-button type="primary" @click="editDone('editForm')">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog
    :title="jurTitle"
    :visible.sync="jurVisible"
    width="420px"
    :before-close="jurClose"
    >
      <div class="web-jurManger-menus">
        <el-row class="tac">
          <el-col :span="24">
            <el-menu ref="jurMenu"
              default-active="1-2"
              class="el-menu-vertical-demo"
            >
              <el-submenu index="1">
                <template slot="title">
                  <i class="el-icon-menu"></i>
                  <span>主菜单</span>
                </template>
                <el-menu-item-group>
                  <el-menu-item index="1-1">
                    <i class="el-icon-s-finance"></i>
                    <span slot="title">历史业务</span>
                  </el-menu-item>
                  <el-menu-item index="1-2">
                    <i class="el-icon-s-help"></i>
                    <span slot="title">当前业务</span>
                  </el-menu-item>
                  <el-menu-item index="1-3">
                    <i class="el-icon-s-marketing"></i>
                    <span slot="title">待办事项</span>
                  </el-menu-item>
                </el-menu-item-group>
              </el-submenu>
              <el-submenu index="1-4">
                <template slot="title">
                  <i class="el-icon-menu"></i>
                  <span slot="title">用户管理</span>
                </template>
                <el-menu-item index="1-5-1">
                  <i class="el-icon-plus"></i>
                  <span slot="title">用户增加</span>
                </el-menu-item>
                <el-menu-item index="1-5-2">
                  <i class="el-icon-finished"></i>
                  <span slot="title">用户编辑</span>
                </el-menu-item>
                <el-menu-item index="1-5-3">
                  <i class="el-icon-s-custom"></i>
                  <span slot="title">角色管理</span>
                </el-menu-item>
                <el-menu-item index="1-5-4">
                  <i class="el-icon-s-fold"></i>
                  <span slot="title">权限管理</span>
                </el-menu-item>
              </el-submenu>
            </el-menu>
          </el-col>
        </el-row>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="jurDone">关 闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { Message } from "element-ui";
import { isNotEmpty, isNotOneOf, isNoLength } from '@/utils/validate';
import { getRolesList } from '@/api/user'
export default {
  name: "RoleManger",
  data() {
    const validateRoleName = (rule, value, callback) => {
      if (!isNotEmpty(value)) {
        callback(new Error("角色不能为空!"))
      } else if (!isNotOneOf(value)){
        callback(new Error("角色填写错误!"))
      } else {
        callback()
      }
    }

    const validateDescription = (rule, value, callback) => {        
      if (!isNotEmpty(value)) {
        callback(new Error("描述不能为空!"))
      } else if(!isNoLength(value)) {
        callback(new Error("描述不能少于6个字！"))
      } else {
        callback()
      }
    }

    return {
      handleTitle: "",
      editTitle: "",
      handleAddVisible: false,
      editVisible: false,
      jurTitle: "",
      jurVisible: false,
      editForm: {
        editRole: "",
        editDescription: ""
      },
      editFormRules: {
        editRole: [
          {
            required: true,
            trigger: "blur",
            validator: validateRoleName
          }
        ],
        editDescription: [
          {
            required: true,
            trigger: "blur",
            validator: validateRoleName
          }
        ]
      },
      addForm: {
        role: "",
        description: ""
      },
      addFormRules: {
        role: [
          {
            required: true,
            trigger: "blur",
            validator: validateRoleName
          }
        ],
        description: [
          {
            required: true,
            trigger: "blur",
            validator: validateDescription
          }
        ]
      },
      tableData: [
        {
          role: "录入员",
          description: "资料录入者"
        },
        {
          role: "查询员",
          description: "资料查询员"
        },
        {
          role: "审核员",
          description: "资料审核员"
        },
        {
          role: "过户员",
          description: "资料过户员"
        },
        {
          role: "开票员",
          description: "资料开票员"
        },
        {
          role: "管理员",
          description: "资料管理员"
        }
      ]
    };
  },
  async beforeMount() {
    const hasData = sessionStorage.getItem('data')
    if (!hasData) {
      await this.$store.dispatch('user/doLoginOut')
      this.$router.push({ path: '/login' })
    } else {
      const { page, size } = JSON.parse(sessionStorage.getItem('data'))
      getRolesList({ page: page, size: size }).then(resp => {
          for(const key of resp.data.content) {                      
            const { role, description } = key
            this.tableData.push({ role: role, description: description })
          }
      })
    }
  },
  methods: {
    handleAddAction() {
      this.handleAddVisible = true;
      this.handleTitle = "新增角色";
    },
    handleCancleAction(formName) {
        this.$refs[formName].resetFields()
        this.handleAddVisible = false
    },
    handleAddDone(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const { role, description } = this.addForm;
          const isIncluded = this.tableData.some(item => {
            return (
              JSON.stringify({
                role: role,
                description: description
              }) === JSON.stringify(item)
            );
          });
          if (isIncluded) {
            Message({
              message: "该角色已经存在，请重新填写",
              type: "error",
              duration: 3 * 1000
            });
            this.$refs[formName].resetFields();
          } else {
            this.tableData.push({
              role: role,
                description: description
            });
            this.handleAddVisible = false;
            this.$refs[formName].resetFields();
          }
        }
      });
    },
    handleAddClose(done) {
      this.$refs['addForm'].resetFields()
      done()
    },
    editClose(done) {
      this.$refs['editForm'].resetFields()
      done()
    },
    jurClose(done) {
      done()
    },
    handleEditAction(index, row) {
      console.log(index);
      console.log(row);
      this.editVisible = true;
      this.editTitle = "编辑角色";
    },
    editAction(formName){
      this.$refs[formName].resetFields()
      this.editVisible = false
    },
    editDone(formName) {
      this.$refs[formName].resetFields()
      this.editVisible = false
    },
    jurDone() {
      this.jurVisible = false
      this.jurTitle = ""
    },
    handleDisableAction(index, row) {
      console.log(index);
      console.log(row);
      this.tableData.splice(index, 1)
    },
    handleAllocationAction(index, row) {
      console.log(index);
      console.log(row);
      this.jurVisible = true
      this.jurTitle = '授权管理'
    }
  }
};
</script>
<style lang="scss">
.web-business {
  margin-top: 32px;
  :last-child {
    a {
      border-bottom: 1px solid #ddd;
    }
  }
  a {
    display: block;
    padding: 30px 0;
    border-top: 1px solid #ddd;
  }
}
.roleManger {
  width: 80%;
  margin: 0 auto;
  .el-menu {
    border-right: 0;
  }
  .handleAdd {
    width: 190px;
    background-color: #4f8ade;
    color: #fff;
  }
}
.el-card__body {
  padding: 0 !important;
  .item {
    padding: 10px 0;
  }
}
</style>
